.mainContainer {
  background-color: greenyellow;

  position: absolute;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  overflow: hidden;
}

.icona {
  width: 50px;
  height: 50px;

  background-color: brown;

  margin: 10px;
}

.absolute {

  position: absolute;

}

.altodestra {
  top: 0;
  right: 0;
}

.altosinistra {
  top: 0;
  left: 0;
}

.bottone {
  padding: 10px;

  background-color: yellow;

  margin: 10px;
}

.start {

  width: 200px;
  bottom: 30px;
  left: calc(50% - 100px);

  font-weight: bold;
  font-size: 3em;
  text-align: center;

  cursor: pointer;
}

.flex {
  display: flex;

  justify-items: center;

  align-items: center;
}