.tavoloContainer {
    width: 100vw;
    height: 100vh;

    background-color: green;
}

.playerFirst {
    position: absolute;

    padding: 10px;

    bottom: 20px;
    left: 50%;

    transform: translateX(-50%);

}
.playerSecond {
    position: absolute;

    padding: 10px;

    top: 20px;
    left: 50%;

    transform: translateX(-50%);

}

.tavolo {
    position: absolute;

    padding: 10px;

    height: 200px;
    width: 100%;

    top: calc(50% - 100px);

    display: flex;
    justify-content: center;

}

.card {
    position: relative;
    height: 125px;
    width: 80px;

    margin: 0px 6px;



    background-color: purple;

    border-radius: 7px;

    overflow: hidden;

   
}
.card img {
    position: absolute;
    width: 89px;
    height: 135px;

    top: -5px;
    left: -5px;

    user-select: none;
    pointer-events: none;
}

.scartaBtn {

    position: absolute;
    width: 70px;
    height: 30px;


    left: calc(50% - 35px);

    border: none;

    border-radius: 10px;
}
.scartaOne{
    top: -45px;
}
.scartaTwo{
    bottom: -45px;
}
.mazzo {
    position: absolute;

    display: flex;
    justify-content: center;
    align-items: center;

    top: 20px;
    right: 20px;
    height: 125px;
    width: 80px;

    background-size: 109%;
    background-position: center;

    border-radius: 7px;

    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
}

.numeroMazzo {

    position: absolute;


    right: 10px;
    bottom: 10px;

    width: 40px;
    height: 40px;

    color: black;
    font-size: 25px;
    font-weight: bold;

    background: white;

    border-radius: 40px;

    text-align: center;

    line-height: 40px;

    pointer-events: none;
    user-select: none;

    box-shadow: 0px 3px 7px rgba(0,0,0,.5);
}

.settebello {

    position: absolute;


    left: 10px;
    top: 10px;

    width: 40px;
    height: 40px;

    color: black;
    font-size: 25px;
    font-weight: bold;

    background: gold;

    border-radius: 40px;

    text-align: center;

    line-height: 40px;

    pointer-events: none;
    user-select: none;

    box-shadow: 0px 3px 7px rgba(0,0,0,.5);
}

.cartePrese {
    position: absolute;

    display: flex;
    justify-content: center;
    align-items: center;

    left: 20px;
    height: 125px;
    width: 80px;

    background-size: 109%;
    background-position: center;

    border-radius: 7px;

    overflow: hidden;

    z-index: 99;
}

.scopeTwo{
    left: 80px;
    z-index: 50;
}
.scopeOne{
    left: 80px;
    z-index: 50;
}

.preseOne {
    bottom: 20px;
}
.preseTwo {
    top: 20px;
}

.attive {
    transform: scale(1);

    transition: 0.2s ease-in-out;

    box-shadow: 0px 3px 5px rgba(0,0,0,.5);
}

/* .attive:hover {
    transform: scale(1.05);
    box-shadow: 0px 3px 20px rgba(0,0,0,.2);

    z-index: 999;
} */

.attiva {
    transform: scale(1.05);
    box-shadow: 0px 3px 20px rgba(0,0,0,.2);

    border-radius: 7px;
}

.attiva:after {
    content:"";

    position: absolute;

    left: 0px;
    top: 0px;

    width: calc(100% - 6px);
    height: calc(100% - 6px);

    z-index: 9999;

    border: solid 3px white;
    border-radius: 5px;
}

.titleOfCard {
    position: absolute;
    top: 50px;
    left: -5px;

    width: calc(100% + 0px);

    padding-right: 10px;

    background-color: orange;

    text-align: right;

    color: white;

    font-size: 20px;

    font-weight: 900;

    transform: rotate(5deg);
}

.punti {
    width: 180px;
    position: absolute;
    left: calc(50% - 90px);

    color: white;

    font-size: 1.2em;

    font-weight: bold;

    text-align: center;
}
.puntiOne {
    bottom: -15px;
}

.puntiTwo {
    top: -15px;
}

.fineMazzo {
    border: solid white 2px;
    color: white;

    font-weight: bold;
}

.fineMazzo span {
    pointer-events: none;
    user-select: none;
}